import React from "react";
import PropTypes from "prop-types";
import { setUrl } from "../Configuration/ducks";
import { connect } from "react-redux";
import { compose, branch, renderNothing } from "recompose";

const withOpen = branch(({ isOpen }) => !isOpen, renderNothing);

const style = {
  position: "absolute",
  bottom: "0px",
  opacity: 0.7,
  width: "100%",
  backgroundColor: "#fff",
  height: "2em",
  padding: 0,
  margin: 0,
  zIndex: 5
};

const ConfigBar = ({ url, onSubmit }) => (
  <div style={style}>
    <form onSubmit={onSubmit}>
      <label>ElasticSearch URL: </label>
      <input type="text" autoFocus defaultValue={url} />
    </form>
  </div>
);

ConfigBar.propTypes = {
  isOpen: PropTypes.bool.isRequired,
  url: PropTypes.string.isRequired,
  onSubmit: PropTypes.func.isRequired
};

const enhance = compose(
  connect(
    state => ({
      isOpen: state.configuration.configBar,
      url: state.configuration.url
    }),
    dispatch => ({
      onSubmit: e => {
        const newUrl = e.target.elements[0].value;
        dispatch(setUrl(newUrl));
        e.preventDefault();
      }
    })
  ),
  withOpen
);

export default enhance(ConfigBar);



